<template>
  <v-card>
      <v-card-actions style="background-color: #6b3e9e; height: 200px;">
          
      </v-card-actions>
  </v-card>
  <v-card class="mx-auto pa-12 pb-8" elevation="8" max-width="448" rounded="lg" style="margin-top: -50px;">
    <div class="text-subtitle-1 text-medium-emphasis">Ti khon</div>
    <v-form v-model="form" @submit.prevent="onSubmit">
      <v-text-field density="compact" placeholder="Ti khon" prepend-inner-icon="mdi-email-outline" variant="outlined"
        v-model="info.Username" :rules="[required]" :readonly="loading" clearable></v-text-field>

      <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">
        Mt khu
      </div>

      <v-text-field :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'" :type="visible ? 'text' : 'password'"
        density="compact" placeholder="Mt khu" prepend-inner-icon="mdi-lock-outline" variant="outlined"
        :rules="[required]" :readonly="loading" @click:append-inner="visible = !visible" v-model="info.Password"
        clearable></v-text-field>

      <div v-if="!isSuccess" style="color: red; font-size: small;">Tn ng nhp hoc mt khu khng chnh xc</div>
      
      <v-btn class="mb-8" color="blue" size="large" variant="tonal" type="submit" block :readonly="loading"
        :disabled="!form" @click.once="onSubmit">
        <div v-if="loading">
          <v-progress-circular indeterminate></v-progress-circular>
        </div>
        <div v-else> ng nhp</div>
      </v-btn>
      <!-- <v-card-actions class="justify-center">
        <a href="/register">ng k</a>
      </v-card-actions> -->
    </v-form>
  </v-card>
</template>
<script>
import Cookies from 'js-cookie';
import { utilController } from '@/services/Util';
import { userController } from '@/services/UserController';
export default {
  data: () => ({
    visible: false,
    form: false,
    info: {
      Username: "",
      Password: ""
    },
    loading: false,
    isSuccess: true,
  }),
  methods: {
    onSubmit() {
      if (!this.form) return
      this.loading = true
      this.isLogin = true
      let obj = {
        userName: this.info.Username,
        password: this.info.Password
      }
      userController.login(obj)
      .then(res => {
        window.location.href = "/admin"
        Cookies.set('token', res.data.token, {
            secure: true,
            sameSite: 'strict'
        })
        Cookies.set('id', res.data.id, {
            secure: true,
            sameSite: 'strict'
        })
        Cookies.set('name', res.data.name, {
            secure: true,
            sameSite: 'strict'
        })
        Cookies.set('guid', res.data.guid, {
            secure: true,
            sameSite: 'strict'
        })
        // this.loading = false
        // if (Array.isArray(res)) {
        //   this.isSuccess = false
        // } else {
        //   Cookies.set('tokenJWT', res, {
        //     secure: true,
        //     sameSite: 'strict'
        //   })
        //   window.location.href = "/"
        // }
      })
      .catch(err=>{
        window.location.href = "/"
      })
      
    },
    required(v) {
      this.isSuccess = true;
      return !!v || 'Khng c b trng'
    }
  },
  created: () => {
    let checkJWT = utilController.checkJWTToken();
    if (checkJWT) {
      window.location.href = "/admin"
    }
  },
}
</script>