<style scoped>
.bg-card {
    font-size: 20px;
    font-weight: 900;
    background-image: url("../../assets/bg-card-footer.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
}

.v-row {
    padding: 0;
    margin: 0;
}

.v-col {
    padding: 0;
    margin: 0;
}

.form {
    width: 70%;
}

@media (max-width: 900px) {
    .form {
        width: auto;
    }
}
</style>
<template v-slot:activator="{ on }">
    <div class="bg-card">
        <v-container fluid>
            <v-card class="mx-auto pa-12 pb-8" elevation="8" max-width="800" rounded="lg">
                 <v-tabs>
                    <v-tab @click="selectInfoView('ThongTinCaNhan')">THNG TIN C NHN</v-tab>
                    <v-tab @click="selectInfoView('TaiKhoan')">TI KHON</v-tab>
                 </v-tabs>
                 <v-sheet v-show="listInfoView.filter(x=>x.name=='ThongTinCaNhan')[0].isShow">
                        <v-row style="margin-top: 20px;">
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Tn y </v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Tn y "
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >in thoi</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="in thoi"
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Ngy sinh</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact" 
                                variant="outlined"
                                type="date" 
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Chc v</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Chc v"
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Tnh/thnh ph</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-select
                                density="compact"
                                variant="outlined"
                                placeholder="Tnh/thnh ph"
                                :items="itemsTinh"
                                ></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Cu lc b</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Cu lc b"
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Trng</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Trng"
                                ></v-text-field>
                            </v-col>
                        </v-row>

                        <v-card-actions class="justify-center">
                            <v-btn style="background-color: green; color: white; margin-bottom: 20px;">XC NHN</v-btn>
                        </v-card-actions>
                 </v-sheet>
                 <v-sheet v-show="listInfoView.filter(x=>x.name=='TaiKhoan')[0].isShow">
                     <!-- Tn ng nhp -->
                    <v-row style="margin-top:15px">
                            <v-col cols="4" class="justify-center">
                                <v-list-subheader style="text-align: center;" >Tn ng nhp</v-list-subheader>
                            </v-col>
                            <v-col col="8">
                                <v-text-field
                                 density="compact"
                                 variant="outlined"
                                 placeholder="Tn ng nhp"
                                ></v-text-field>
                            </v-col>
                    </v-row>
                    <!-- Email -->
                    <v-row>
                        <v-col cols="4" class="justify-center">
                            <v-list-subheader style="text-align: center;" >Email</v-list-subheader>
                        </v-col>
                        <v-col col="8">
                            <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Email"
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <!-- Mt khu -->
                    <v-row>
                        <v-col cols="4" class="justify-center">
                            <v-list-subheader style="text-align: center;" >Mt khu</v-list-subheader>
                        </v-col>
                        <v-col col="8">
                            <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Mt khu"
                                type="password"
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <!-- Nhp li Mt khu -->
                    <v-row>
                        <v-col cols="4" class="justify-center">
                            <v-list-subheader style="text-align: center;" >Nhp li mt khu</v-list-subheader>
                        </v-col>
                        <v-col col="8">
                            <v-text-field
                                density="compact"
                                variant="outlined"
                                placeholder="Nhp li mt khu"
                                type="password"
                            ></v-text-field>
                        </v-col>
                    </v-row>
                    <v-card-actions class="justify-center">
                        <v-btn style="background-color: green; color: white; margin-bottom: 20px;">XC NHN</v-btn>
                    </v-card-actions>
                 </v-sheet>
            </v-card>
        </v-container>
    </div>

</template>
<script>
import Footer from './layout/Footer.vue';

export default {
    data() {
        return {
            isShowPass: false,
            tab: null,
            listInfoView:[
                {
                    name: "ThongTinCaNhan",
                    isShow: true
                },
                {
                    name: "TaiKhoan",
                    isShow: false
                }
            ]
        }
    },
    methods: {
        selectInfoView(name){
            this.listInfoView.filter(x=>x.name!=name).map(x=>x.isShow=false)
            this.listInfoView.filter(x=>x.name==name)[0].isShow = true
        }
    },
    components: {
        
    }
}
</script>
